.close-icon:before,
.close-icon:after {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  font-weight: 600;
  background-color: rgba(247, 242, 242, 0.953);
  top: 50%;
  left: 0;
  transition: transform 0.3s ease;
}

.close-icon:before {
  transform: translateY(-50%) rotate(45deg);
}

.close-icon:after {
  transform: translateY(-50%) rotate(-45deg);
}

.close-icon:hover:before,
.close-icon:hover:after {
  transform: translateY(-50%) rotate(360deg);
}

